
"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { useToast } from "@/hooks/use-toast";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { DropdownMenu, DropdownMenuCheckboxItem, DropdownMenuContent, DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { createDifferentiatedMaterialsAction } from "./actions";
import type { CreateDifferentiatedMaterialsOutput } from "@/ai/flows/create-differentiated-materials";
import { Loader2, UploadCloud, Download, Save, ChevronDown } from "lucide-react";
import Image from 'next/image';
import { Badge } from "@/components/ui/badge";


const gradeLevels = [
  { id: "Grade 1", label: "Grade 1" },
  { id: "Grade 2", label: "Grade 2" },
  { id: "Grade 3", label: "Grade 3" },
  { id: "Grade 4", label: "Grade 4" },
  { id: "Grade 5", label: "Grade 5" },
  { id: "Grade 6", label: "Grade 6" },
  { id: "Grade 7", label: "Grade 7" },
  { id: "Grade 8", label: "Grade 8" },
  { id: "Grade 9", label: "Grade 9" },
  { id: "Grade 10", label: "Grade 10" },
] as const;


const worksheetsSchema = z.object({
  photo: z.any().refine(file => file instanceof File, "A photo of the textbook page is required."),
  gradeLevels: z.array(z.string()).refine((value) => value.some((item) => item), {
    message: "You have to select at least one grade level.",
  }),
  lessonName: z.string().optional(),
  numberOfQuestionsPerGrade: z.coerce.number().optional(),
});

type WorksheetsFormValues = z.infer<typeof worksheetsSchema>;

export default function DifferentiatedWorksheetsPage() {
  const { toast } = useToast();
  const [preview, setPreview] = useState<string | null>(null);
  const [generatedWorksheets, setGeneratedWorksheets] = useState<CreateDifferentiatedMaterialsOutput['worksheets'] | null>(null);
  const [pending, setPending] = useState(false);

  const form = useForm<WorksheetsFormValues>({
    resolver: zodResolver(worksheetsSchema),
    defaultValues: {
      photo: undefined,
      gradeLevels: ["Grade 4"],
      lessonName: "",
      numberOfQuestionsPerGrade: 5,
    },
  });

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      form.setValue("photo", file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setPreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };
  
  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result as string);
      reader.onerror = error => reject(error);
    });
  };

  async function onSubmit(data: WorksheetsFormValues) {
    setPending(true);
    setGeneratedWorksheets(null);
    try {
      const photoDataUri = await fileToBase64(data.photo);
      const result = await createDifferentiatedMaterialsAction({
        ...data,
        photoDataUri
      });
      if (result.worksheets && result.worksheets.length > 0) {
        setGeneratedWorksheets(result.worksheets);
      } else {
        toast({
          variant: "destructive",
          title: "Error",
          description: "Failed to generate worksheets. The AI might not have understood the image. Please try a clearer photo.",
        });
      }
    } catch (error) {
      toast({
        variant: "destructive",
        title: "Error",
        description: "An unexpected error occurred.",
      });
    } finally {
      setPending(false);
    }
  }

  const handleSave = () => {
    toast({ title: "Saved to My Library (Not Implemented)" });
  };
  
  const handleDownload = (content: string, grade: string) => {
    // This is a mock for PDF generation. In a real app, use jsPDF.
    const blob = new Blob([content], { type: 'text/plain;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `worksheet-${grade.replace(' ', '-')}.txt`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
    toast({ title: "Worksheet downloaded as a text file." });
  };
  
  const selectedGrades = form.watch("gradeLevels");

  return (
    <div className="space-y-6">
      <PageHeader
        title="Differentiated Worksheets"
        description="Upload a photo of a textbook page to generate worksheets for different grades."
      />
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
          <Card>
            <CardHeader>
              <CardTitle className="font-headline">Worksheet Request</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <FormField
                control={form.control}
                name="photo"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Textbook Page Photo</FormLabel>
                    <FormControl>
                      <div className="relative flex justify-center w-full h-48 px-6 pt-5 pb-6 border-2 border-dashed rounded-md border-input">
                        <div className="space-y-1 text-center">
                          {preview ? (
                            <Image src={preview} alt="Preview" width={150} height={150} className="mx-auto h-32 w-auto object-contain rounded-md" />
                          ) : (
                            <>
                              <UploadCloud className="w-12 h-12 mx-auto text-muted-foreground" />
                              <p className="text-sm text-muted-foreground">Tap to upload a photo</p>
                            </>
                          )}
                          <Input
                            type="file"
                            accept="image/*"
                            className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
                            onChange={handleFileChange}
                          />
                        </div>
                      </div>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="gradeLevels"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Select Grade Levels</FormLabel>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="outline" className="w-full justify-between">
                          <div className="flex gap-2 flex-wrap">
                            {selectedGrades.length > 0 ? (
                               selectedGrades.sort((a,b) => parseInt(a.split(' ')[1]) - parseInt(b.split(' ')[1])).map(grade => <Badge key={grade} variant="secondary">{grade}</Badge>)
                            ) : (
                              <span>Select grades</span>
                            )}
                          </div>
                          <ChevronDown className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent className="w-[--radix-dropdown-menu-trigger-width]">
                        <DropdownMenuLabel>Grades</DropdownMenuLabel>
                        <DropdownMenuSeparator />
                        {gradeLevels.map((item) => (
                          <DropdownMenuCheckboxItem
                            key={item.id}
                            checked={field.value?.includes(item.id)}
                            onCheckedChange={(checked) => {
                              return checked
                                ? field.onChange([...field.value, item.id])
                                : field.onChange(
                                    field.value?.filter(
                                      (value) => value !== item.id
                                    )
                                  );
                            }}
                          >
                            {item.label}
                          </DropdownMenuCheckboxItem>
                        ))}
                      </DropdownMenuContent>
                    </DropdownMenu>
                     <FormDescription>
                        Choose one or more grades to generate worksheets for.
                      </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                 <FormField
                  control={form.control}
                  name="lessonName"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Lesson Name/Topic (Optional)</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., Photosynthesis, Rivers of India" {...field} />
                      </FormControl>
                    </FormItem>
                  )}
                />
                 <FormField
                  control={form.control}
                  name="numberOfQuestionsPerGrade"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Questions per Grade (Optional)</FormLabel>
                      <FormControl>
                        <Input type="number" placeholder="e.g., 5" {...field} />
                      </FormControl>
                    </FormItem>
                  )}
                />
              </div>

            </CardContent>
            <CardFooter>
              <Button type="submit" disabled={pending}>
                {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Generate Worksheets
              </Button>
            </CardFooter>
          </Card>
        </form>
      </Form>
      
       {pending && (
        <Card>
          <CardHeader>
            <CardTitle className="font-headline flex items-center gap-2">
              <Loader2 className="h-5 w-5 animate-spin text-primary" />
              <span>Generating Worksheets...</span>
            </CardTitle>
            <CardDescription>Sahayak is analyzing the page and creating materials. This may take a moment.</CardDescription>
          </CardHeader>
           <CardContent>
            <div className="space-y-2">
               <div className="h-4 bg-muted rounded animate-pulse w-full"></div>
               <div className="h-4 bg-muted rounded animate-pulse w-5/6"></div>
               <div className="h-4 bg-muted rounded animate-pulse w-full"></div>
               <div className="h-4 bg-muted rounded animate-pulse w-4/6"></div>
            </div>
          </CardContent>
        </Card>
      )}

      {generatedWorksheets && (
        <Card>
           <CardHeader>
            <CardTitle className="font-headline">Generated Worksheets</CardTitle>
            <CardDescription>Here are the differentiated materials for your selected grades. You can view, download, or save them.</CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs defaultValue={generatedWorksheets[0].gradeLevel}>
              <TabsList>
                {generatedWorksheets.map(ws => (
                   <TabsTrigger key={ws.gradeLevel} value={ws.gradeLevel}>{ws.gradeLevel}</TabsTrigger>
                ))}
              </TabsList>
              {generatedWorksheets.map(ws => (
                <TabsContent key={ws.gradeLevel} value={ws.gradeLevel} className="mt-4">
                   <div className="space-y-4">
                    <div>
                      <h3 className="text-lg font-semibold font-headline mb-2">Worksheet</h3>
                      <pre className="whitespace-pre-wrap font-body text-base leading-relaxed bg-muted p-4 rounded-md">{ws.worksheetContent}</pre>
                    </div>
                     <div>
                      <h3 className="text-lg font-semibold font-headline mb-2">Answer Key</h3>
                      <pre className="whitespace-pre-wrap font-body text-base leading-relaxed bg-muted p-4 rounded-md">{ws.answerKey}</pre>
                    </div>
                     <div className="flex gap-2 pt-4">
                        <Button variant="outline" onClick={() => handleDownload(ws.worksheetContent + '\n\n---\n\n' + ws.answerKey, ws.gradeLevel)}><Download className="mr-2" /> Download</Button>
                        <Button variant="outline" onClick={handleSave}><Save className="mr-2" /> Save to Library</Button>
                     </div>
                   </div>
                </TabsContent>
              ))}
            </Tabs>
          </CardContent>
        </Card>
      )}

    </div>
  );
}
